import Vue from 'vue'
import Vuex from 'vuex'

import vuexI18n from 'vuex-i18n'

/* Modules */
import { forms } from '../components/both/store'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		name: 'Marcius',
		mail: 'mail@marcius.co',
		phone: '+79771446698',
	},
	modules: {
		forms,
	},
})

/* i18n settings */
Vue.use(vuexI18n.plugin, store)

Vue.i18n.add('en', require('./i18/en.json'))
Vue.i18n.add('ru', require('./i18/ru.json'))

Vue.i18n.set('en')

/* i18n checks LocalStorage */
if (!window.localStorage.getItem('lang')) window.localStorage.setItem('lang', 'en')
else Vue.i18n.set(window.localStorage.getItem('lang'))

export default store
